import React,{ Component } from 'react'
import RaisedButton from 'material-ui/RaisedButton';
import ImagePhoto from 'material-ui/svg-icons/image/photo';
import getimgurl from './support/getimgurl';
import hiro from './photo/hiro.png';
import bkimg from './photo/bb.jpg';
import pan from './photo/pan.png';
import pinch from './photo/pinch.png';
import rotate from './photo/rotate.png';
import IconMenu from 'material-ui/IconMenu';
import ActionHelp from 'material-ui/svg-icons/action/help';
import IconButton from 'material-ui/IconButton';
import MenuItem from 'material-ui/MenuItem';

const styles = {
    container: {
        minHeight: '100%',
        position: 'absolute',
        top: 0,
        left: 0,
        right: 0,
        backgroundImage: `url(${bkimg})`,
        backgroundRepeat: 'no-repeat',
        backgroundSize: 'cover',
        backgroundPositionX: '50%',
        paddingTop: 30,
        paddingLeft: 10,
        paddingRight: 10,
        fontSize: '1.2rem',
    },
    button:{
    		margin:12,
    },
    expamleImageInput:{
    		crusor:'pointer',
    		position:'absolute',
    		top:0,
    		bottom:0,
    		right:0,
    		left:0,
    		width:'40%',
    		opacity:0,
    },

    list: {
        paddingRight: 40,
    },

    listItem: {
        paddingBottom: 15,
    },

    title: {
        fontSize: '2rem',
        textAlign: 'center',
        fontWeight: 'bold',
    },

    a: {
        textDecoration: 'overline underline',
    },

    hiroMarker: {
        textAlign: 'left',
    },

    btnFileInput: {
        marginTop: 15,
        marginBottom: 10,
        marginLeft:15,
        border:'2px solid white',
    },

	hiroMarkerImg: {
        marginTop: '1rem',
        height: '5rem',
        width: '5rem',
        border: '5px solid white',
    },
    	gestureImg: {
        marginTop: '1rem',
        marginLeft:'3rem',
        height: '5rem',
        width: '5rem',
        border: '5px solid white',
    },

    fileInput: {
        display: 'none',
    },
	helpicon:{
		margin:12,
	},
    hr: {
        border: 0,
        borderTop: '2px solid white',
        marginBottom: '1rem',
        marginTop: '1rem',
    },
};
class Fileselect extends Component {

	handleChange = (event) => {
        var reader = new FileReader();
        reader.addEventListener('load', () => {
            getimgurl(reader.result)
                .then(this.props.onFileSelected);
        }, false);

        reader.readAsDataURL(event.target.files[0]);
    }
	
	handleFileInputClick =()=>{
		this.fileInput.click();
	}
    storeFileInputRef = node => {
        this.fileInput = node;
    }
    handleGesture= () => {
    		alert("1:标记不要打印太大 2:ar.js库能根据角度距离调节图像位置大小");
    }

	render() {

		return (
			<div style={styles.container}>
				<h1 style={styles.title}>SKETCH-AR</h1>
				<hr style={styles.hr}/>
				<h4> 应用使用说明</h4	>
				<ol style={styles.list}>
					<li style={styles.listItem}>
						<div>
							这是一个 <a style={styles.a} href={hiro}>hiro marker</a>
						</div>
					</li>
					<li style={styles.listItem}>
						把这个标记放在白纸上
					</li>
					<li style={styles.listItem}>
						从你的相册中选择素描图片
						<div>
							<RaisedButton 
								style={styles.btnFileInput}
								primary   
								htmlFor="file_input" 
								onTouchTap={this.handleFileInputClick} 
								label="从相册选取" 
								labelPositon="before" 
								icon={<ImagePhoto/>}
							>
								<input 
									ref={this.storeFileInputRef} 
									id="file_input" 
									style={styles.fileInput} 
									type="file" 
									accept="image/*"
									onChange={this.handleChange}
								/>
                            </RaisedButton>
                       		<IconMenu
                       			style={styles.helpicon}
                       			iconButtonElement={<IconButton><ActionHelp /></IconButton>}
                  			    anchorOrigin={{horizontal:'left',vertical:'bottom'}}
                  			    argetOrigin={{horizontal:'left',vertical:'bottom'}}
            				     >
                 			      <MenuItem  href={hiro} primaryText="图标样式"/>
                   				  <MenuItem  primaryText="注意事项" onTouchTap={this.handleGesture}/>
             			   </IconMenu>
                        </div>
                     </li>
                     <li style={styles.listItem}>
                     	待获取摄像头权限后,讲摄像头对准标记
                     </li>
                     <li style={styles.listItem}>
                     	<div>
                     		可通过(1)Pan(2)Pinch(3)Rotate手势操作:
                     	</div>
                     	<div>
                     		<img style={styles.gestureImg} alt="Pan Gesture" src={pan} />
                     		<img style={styles.gestureImg} alt="pinch Gesture" src={pinch} />
                     		<img style={styles.gestureImg} alt="rotate Gesture" src={rotate} />
                     	</div>
                     </li>
                     <li>
                     	根据需要从设置里调节图片属性
                 	</li>
                 </ol>
              </div>
		);
	}	
}
export default Fileselect;


